import styled from "styled-components";

export const SeatStatusWrapper = styled.div`
  max-height: 700px;
  overflow-y: auto;
  margin-bottom: 20px;
  .ant-card-head-title {
    font-size: 16px;
    font-weight: 700;
    flex: none;
  }
  .ant-card-head,
  .ant-card-body {
    padding: 0;
  }

  .ant-card-extra {
    float: left;
    margin-left: 120px;
  }
  .status {
    display: inline-block;
    margin-right: 30px;
    cursor: pointer;
    span {
      color: #4b4b4b;
      font-size: 14px;
    }
  }
  .seat-status-content {
    font-size: 0;
    max-height: 600px;
    overflow-y: auto;
    .ant-col {
      margin-bottom: 20px;
    }
    .seat-status-content-item {
      display: inline-block;
      padding: 0 9px;
      width: 150px;
      height: 85px;
      border: solid 1px #e5e5e5;
      margin-top: 20px;
      margin-right: 20px;
      cursor: pointer;
      text-align: center;
      .trapezoid-bodyname {
        position: absolute;
        top: 7%;
        left: 16%;
        width: 90px;
        font-size: 14px;
        color: #fff;
        line-height: 29px;
        overflow: hidden;
        text-overflow: ellipsis;
        -o-text-overflow: ellipsis;
        white-space: nowrap;
      }
      .seatName {
        color: #202020;
        font-size: 14px;
        overflow: hidden;
        text-overflow: ellipsis;
        -o-text-overflow: ellipsis;
        white-space: nowrap;
        width: 100px;
        margin: 5px auto;
      }
    }
  }
`;
export const Layout = styled.div`
  .layout-header {
    margin: 15px 0px;
    .ant-form-item {
      margin-right: 0px;
    }
    .ant-btn-primary + .ant-btn-primary {
      margin-left: 20px;
    }
    
  }
  .tableStyle .ant-table{
      overflow-x: auto;
  }
  
  td span {
      cursor: pointer;
  }
  .curSelectRow{
      background-color: #f0f8ff;
  }
  
  //.tableStyle .ant-table-content {
  //    min-width: 800px;
  //    overflow-x: auto;
  //}
`;
export const FormLayOut = styled.div`
  .ant-form-item label {
    position: relative;
    width: 92px;
    //text-align: right;
    display: inline-block;
  }
  .ant-form-inline .ant-form-item-with-help{
    margin-bottom: 0 !important
  }
`;
export const Trapezoid = styled.div`
  position: relative;
  margin-top: ${props => props.marginTop + "px"};
  width: ${props => props.width + "px"};
  height: ${props => props.height + "px"};
`;
export const TrapezoidHeader = styled.div`
  height: ${props => props.height + "px"};
  background-color: ${props => props.color};
`;
export const TrapezoidContent = styled.div`
  display: inline-block;
  width: ${props => props.width + "px"};
  height: 0;
  border-right: ${props => props.borderRight + "px"} solid transparent;
  border-left: ${props => props.borderLeft + "px"} solid transparent;
  border-top: ${props => props.borderTop + "px"} solid ${props => props.color};
`;