import React, { PureComponent } from "react";
import { connect } from "react-redux";
import {
  SeatStatusWrapper,
  Trapezoid,
  TrapezoidContent,
  TrapezoidHeader
} from "./style";
import { Card, message, Row, Tag, Button } from "antd";
import {
  ModalForm,
  StorageExitSettingModalForm,
  StorageModalForm
} from "./seatStatusModal";
import * as api from "../../../axios";
import AuthComponent from "../../common/authComponent ";
const AuthButton = AuthComponent(Button);
const mapStageToTitle = {
  1: "钢筋绑扎台座状态",
  10: "制梁台座状态",
  20: "存梁台座状态"
};
class SeatStatus extends PureComponent {
  componentDidMount() {
    const stage = this.props.history.location.query
      ? this.props.history.location.query.stage
      : 1;
    this.setState({ stage },()=>{
        this.getPedestralStatus();
    });
  }
  //台座点击
  handleEdit = current => {
    const authCodes  = this.props.authCode.toJS();
    const AUTHCODE = "77003002,77009002,77008002";
    const AUTHCODES = AUTHCODE.split(",");
    if(this.state.stage == 1){
        if (authCodes && !authCodes.funAuthCode.includes(AUTHCODES[0])) {
            message.warning("当前用户无操作权限！");
            return;
        }
    } else if(this.state.stage == 10) {
        if (authCodes && !authCodes.funAuthCode.includes(AUTHCODES[2])) {
            message.warning("当前用户无操作权限！");
            return;
        }
    } else if(this.state.stage == 20) {
        if (authCodes && !authCodes.funAuthCode.includes(AUTHCODES[1])) {
            message.warning("当前用户无操作权限！");
            return;
        }
    }
    
    const { stage } = this.state;
    // 1存梁台座点击空闲台座时弹框提示：当前台座空闲，无法操作！
    // 2点击使用中的台座弹框“存梁台座状态”，列出当前台座的存储梁体列表
    if ((stage == 20 || stage == 10) && !current.stateId) {
      message.warning("当前台座空闲，无法操作！");
      return;
    }
    if (stage == 20 && current.stateId) {
      this.getBodyStored(current.id)
    } else {
      this.getBodyInfo(current);
    }
  };
  //获取存梁列表信息 pedestalId台座id
  getBodyStored(pedestalId){
    api.getBodyStored(pedestalId).then(res => {
      const storedList = res.data.data.items;
      this.setState({ storageModalVisible: true, storedList},);
    });
  }
  //出场
  handleExitSettings = async current => {
    console.log(current);
    // 获取负责人列表
    const {
      data: {
        data: { items: userList }
      }
    } = !!current && (await api.getRemindUsers(current.nextStateId));

    this.setState({
      storageExitSettingModalVisible: true,
      current: {
        ...current,
        userList: userList
      }
    });
  };

  state = {
    stage: 1, //1钢筋绑扎 10制梁 20存梁
    seatList: [],
    visible: false,
    storageModalVisible: false,
    storageExitSettingModalVisible: false,
    current: {},
    bordered: false,
    textAreaValue: "",
    disableSelect:true,
    columns: [
      {
        title: "桥梁名称",
        dataIndex: "bridgeName"
      },
      {
        title: "桩号范围",
        dataIndex: "mileage"
      },
      {
        title: "梁体类型",
        dataIndex: "typeName"
      },
      {
        title: "梁体名称",
        dataIndex: "name"
      },
      {
        title: "操作",
        render: (text, record) => (
          <Tag
            color={"#4C8BFD"}
            style={{ cursor: "pointer" }}
            onClick={() => this.handleExitSettings(record)}
          >
            {"出场"}
          </Tag>
        )
      }
    ],
    storedList: []
  };

  // textarea
  handleInputValue = e => {
    this.setState({
      textAreaValue: e.target.value
    });
  };
  hanldeBodyAvailable = (bridgeId) => {
    api.getBodyAvailable(bridgeId).then(res => {
      const data = res.data.data.items;
      this.setState({
        current: {
          ...this.state.current,
          freeStatusTypeNameList:data,
        },
        disableSelect:false
      });
    });
  };
  handleBridgeChange = (value, typeId) => {
    const bridgeId = value;
    if (bridgeId) {
      this.hanldeBodyAvailable(bridgeId);
    }
  };
  handleBodyNameChange = (value, item) => {
    const bodyId = value;
    this.setState({
      current: {
        ...this.state.current,
        mileage:item.mileage,
        typeName:item.typeName,
      },
      disableSelect:false
    });
  };
  //获取项目部下用户列表
  getDeptUsersList() {
    return api
      .getDepartmentUserList(this.props.beamInfo.toJS().deptId)
      .then(res => {
        return res.data.data.items
      });
  }

  //获取台座状态信息
  getPedestralStatus = () => {
    api.getPedestralStatus(this.state.stage).then(res => {
      this.setState({ seatList: res.data.data.items });
    });
  };

  //获取梁体信息
  getBodyInfo = async current => {
    //获取梁体类型 桥梁列表
    const { bodyTypes, bridges,states} = this.props.beamInfo.toJS();
    //获取当前台座类型
    const { stage } = this.state;
    //空闲状态
    if (!current.bodyId) {
    // 获取负责人列表
      let {data: {data: { items: userList }}} = await api.getRemindUsers(states[0].id);
      if(!userList){
        userList = await this.getDeptUsersList();
      }
      // 获取台座列表
      const {
        data: {
          data: { items: pedestalList }
        }
      } = await api.getPedestalAvailable(stage,current.bodyId);
      // 空闲状态下下一步状态 台座名称 台座类型 均禁用 传当前点击的台座信息即可
      this.setState({
        current: {
          ...current, //当前台座信息
          stateId:states[0].id,
          stageName: "钢筋绑扎台座",
          stateName: states[0].name,
          curStage: stage, //当前状态
          nextStage: "", //下一步状态
          userList: userList, //负责人
          pedestalList: pedestalList, //台座列表
          pedestalId: current.id, //当前台座id
          bodyTypes: bodyTypes, //梁体类型列表
          bridges: bridges, //桥梁列表
          freeStatusTypeNameList: [], //空闲状态下梁体名称列表
          freeStatusMileageNameList: [] //空闲状态下桩号列表,
        },
        visible: true,
        title: mapStageToTitle[stage]
      });
      return;
    }
    // 获取下一步状态
    const {
      data: { data: next }
    } = await api.getBodyNext(current.bodyId);
    // 获取负责人列表
    const {
      data: {
        data: { items: userList }
      }
    } = !!next && (await api.getRemindUsers(next.stateId));
    // 获取台座列表
    const {
      data: {
        data: { items: pedestalList }
      }
    } = !!userList && (await api.getPedestalAvailable(next.stage,current.bodyId));
    console.log("next", next);
    !!pedestalList &&
      this.setState({
        current: {
          ...current,
          first: next.first,
          mileage: next.mileage,
          stageName: next.stageName,
          stateName: next.stateName,
          stateId: next.stateId,
          curStage: stage,
          nextStage: next.stage,
          beginDate: next.beginDate,
          remark: next.remark,
          userName: next.userName,
          bridgeName: next.bridgeName,
          typeName: next.typeName,
          userList: userList,
          pedestalList: pedestalList,
          pedestalId: "",
          bodyTypes: bodyTypes,
          bridges: bridges
        },
        textAreaValue:next.remark,
        visible: true,
        title: mapStageToTitle[stage]
      });
  };
  /* 钢筋绑扎台座状态 预制台座状态 关闭弹窗*/
  handleCancel = () => {
    this.setState({
      visible: false,
      textAreaValue: ""
    },()=>{
        this.getPedestralStatus()
    });

  };
  /* 存梁台座状态 关闭弹窗*/
  handleStorageCancel = () => {
    this.setState({
      storageModalVisible: false,
      textAreaValue: ""
    });
  };

  /* 出场设置 关闭弹窗*/
  handleStorageExitSettingCancel = (current) => {

    this.getBodyStored(current.pedestalId)
    this.getPedestralStatus()
    this.setState({
      storageExitSettingModalVisible: false,
      textAreaValue: ""
    });

  };

  render() {
    const {
      visible,
      title,
      current,
      textAreaValue,
      storedList,
      bordered,
      seatList,
      storageModalVisible,
      columns,
      disableSelect,
      storageExitSettingModalVisible,
      stage
    } = this.state;
    const authCode = this.props.authCode.toJS();
    return (
      <SeatStatusWrapper>
        {seatList.map((seat, i) => (
          <Card
            title={seat.areaName}
            key={i}
            extra={
              <>
                {seat.summaries.map(item => {
                  if (item.color) {
                    item.color = item.color.replace(/O/g, 0);
                  } else {
                    item.color = "#e6e6e6";
                  }
                  return (
                    <a className="status" key={item.name}>
                      <Trapezoid
                        width={28}
                        height={13}
                        marginTop={0}
                        style={{
                          fontSize: 0,
                          display: "inline-block",
                          verticalAlign: "middle",
                          marginRight: "5px"
                        }}
                      >
                        {/* 梁体名称*/}
                        <TrapezoidHeader
                          color={item.color}
                          height={4}
                          style={{ marginLeft: "-3px" }}
                        ></TrapezoidHeader>
                        <TrapezoidContent
                          width={25}
                          borderRight={6}
                          borderLeft={6}
                          borderTop={9}
                          color={item.color}
                        ></TrapezoidContent>
                      </Trapezoid>
                      <span style={{ verticalAlign: "middle" }}>
                        {item.name}： {item.count}
                      </span>
                    </a>
                  );
                })}
              </>
            }
            bordered={bordered}
          >
            <Row className="seat-status-content">
              {seat.status.map(item => {
                if (item.color) {
                  item.color = item.color.replace(/O/g, 0);
                } else {
                  item.color = "#e6e6e6";
                }
                return (
                  <a
                    key={item.id}
                    className="seat-status-content-item"
                    onClick={() => this.handleEdit(item)}
                  >
                    <Trapezoid width={130} height={36} marginTop={16}>
                      {/* 梁体名称*/}
                      <TrapezoidHeader
                        color={item.color}
                        height={7}
                      ></TrapezoidHeader>
                      <TrapezoidContent
                        width={105}
                        borderRight={13}
                        borderLeft={13}
                        borderTop={29}
                        color={item.color}
                      ></TrapezoidContent>
                      {stage == 20 ? (
                        <span
                          className="trapezoid-bodyname"
                          title={item.bodyCount}
                        >
                          {item.bodyCount}
                        </span>
                      ) : (
                        <span
                          className="trapezoid-bodyname"
                          title={item.bodyName}
                        >
                          {item.bodyName ? item.bodyName : "空闲"}
                        </span>
                      )}
                    </Trapezoid>
                    {/* 台座名称*/}
                    <div className="seatName" title={item.name}>
                      {item.name}
                    </div>
                  </a>
                );
              })}
            </Row>
          </Card>
        ))}
        {/* 钢筋绑扎台座状态 预制台座状态*/}
        <ModalForm
          handleCancel={this.handleCancel}
          handleBridgeChange={this.handleBridgeChange}
          handleBodyNameChange={this.handleBodyNameChange}
          visible={visible}
          title={title}
          current={current}
          disableSelect={disableSelect}
          textAreaValue={textAreaValue}
          handleInputValue={this.handleInputValue}
        ></ModalForm>
        {/* 存梁台座状态 */}
        <StorageModalForm
          handleStorageCancel={this.handleStorageCancel}
          visible={storageModalVisible}
          storedList={storedList}
          columns={columns}
        ></StorageModalForm>
        {/* 出场设置 */}
        <StorageExitSettingModalForm
          handleStorageExitSettingCancel={this.handleStorageExitSettingCancel}
          visible={storageExitSettingModalVisible}
          current={current}
          textAreaValue={textAreaValue}
          handleInputValue={this.handleInputValue}
        ></StorageExitSettingModalForm>
      </SeatStatusWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    beamInfo: state.getIn(["login", "beamInfo"]),
    authCode: state.getIn(["login", "authCode"]),
  };
};
export default connect(
  mapStateToProps,
  null
)(SeatStatus);
