import React from "react";
import {Button, Col, DatePicker, Form, Input, message, Modal, Row, Select, Table,} from "antd";
import moment from "moment";
import {FormLayOut} from "./style";
import {TextAreaSpan} from "../progressManagement/style";
import * as api from "../../../axios";

const {Option} = Select;
const FormItem = Form.Item;
const {TextArea} = Input;
const {confirm} = Modal;

// 存梁台座状态弹窗
export const StorageModalForm = (props) => {
    const {visible, handleStorageCancel, columns, storedList} = props;
    return (
        <Modal
            maskClosable={false}
            width={750}
            title="存梁台座状态"
            visible={visible}
            onCancel={handleStorageCancel}
            footer={null}
        >
            <Table rowKey="id" columns={columns} dataSource={storedList} pagination={false} bordered/>
        </Modal>
    )
}

// 存量台座出场设置弹窗
export const StorageExitSettingModalForm = Form.create()(props => {
    const {form, visible, handleStorageExitSettingCancel, current, handleInputValue, textAreaValue} = props;
    const {getFieldDecorator} = form;
    if (Object.keys(current).length == 0) {
        return null
    }
    const handleOk = () => {
        form.validateFields((err, fieldsValue) => {
            if (err) return;
            const params = {
                "beginDate": fieldsValue['planBeginDate'].format('YYYY-MM-DD'),
                "bodyId": current.id,
                "pedestalId": current.pedestalId,
                "remark": textAreaValue,
                "stateId": current.nextStateId,
                "userName": fieldsValue['userName']
            }
            api.setBodyNext(params).then(res => {
                if (res.code == 1) {
                    message.success('设置成功');
                    handleStorageExitSettingCancel(current)
                }
            })
        });
    };
    const disabledEndDate = (endValue) => {
        const defaultValue = current.planBeginDate;
        const currentTime = moment();
        const startValue = currentTime;
        if (!endValue || !startValue) {
            return false;
        }
        return  endValue.valueOf() < defaultValue;
    }
    return (
        <Modal
            maskClosable={false}
            width={800}
            title={'出场设置'}
            visible={visible}
            onCancel={handleStorageExitSettingCancel}
            destroyOnClose
            footer={[
                <Button key="ok" type="primary" onClick={handleOk}>
                    确定
                </Button>,
                <Button key="cancle" onClick={()=>handleStorageExitSettingCancel(current)}>
                    取消
                </Button>
            ]}
        >
            <FormLayOut>
                <Form onSubmit={handleOk} layout="inline" labelAlign='left'>
                    <Row>
                        <Col span={12}>
                            <FormItem label="下一步状态:" style={{height: 65}}>
                                {getFieldDecorator('nextStateName', {
                                    rules: [
                                        {
                                            required: true,
                                        }
                                    ],
                                    initialValue: current.nextStateName,
                                })(<Input disabled style={{
                                    width: 250
                                }}/>)}
                            </FormItem>
                            <FormItem label="桥梁名称" style={{height: 65}}>
                                {getFieldDecorator('bridgeName', {
                                    rules: [
                                        {
                                            required: true,
                                        }
                                    ],
                                    initialValue: current.bridgeName,
                                })(<Input
                                    disabled style={{
                                    width: 250
                                }}/>)}
                            </FormItem>
                            <FormItem label="梁体类型" style={{height: 65}}>
                                {getFieldDecorator("typeName", {
                                    initialValue: current.typeName,
                                    rules: [
                                        {
                                            required: true,
                                        }
                                    ],
                                })(<Input
                                    disabled style={{
                                    width: 250
                                }}/>)}
                            </FormItem>
                            <FormItem label="梁体名称" style={{height: 65}}>
                                {getFieldDecorator('name', {
                                    rules: [
                                        {
                                            required: true,
                                        }
                                    ],
                                    initialValue: current.name,
                                })(<Input disabled style={{
                                    width: 250
                                }}/>)}
                            </FormItem>
                            <FormItem label="负责人" style={{height: 65}}>
                                {getFieldDecorator('userName', {
                                    rules: [
                                        {
                                            required: true,
                                            message: '请选择负责人!'
                                        }
                                    ],
                                    // initialValue: current.userName,
                                })(
                                    <Select style={{width: 250}}
                                            placeholder="请选择负责人">
                                        {
                                            current.userList.length &&
                                            current.userList.map(item => (
                                                <Option value={item.userName}
                                                        key={item.userName}>{item.realName}</Option>
                                            ))
                                        }
                                    </Select>
                                )}
                            </FormItem>
                        </Col>
                        <Col
                            span={12}
                            style={{
                                height: 65
                            }}
                        >
                            <FormItem label="开始时间" style={{height: 65}}>
                                {getFieldDecorator("planBeginDate", {
                                    initialValue: current.planBeginDate
                                        ? moment(current.planBeginDate)
                                        : null,
                                    rules: [
                                        {
                                            required: true,
                                            message: '请选择开始时间!'
                                        }
                                    ],
                                })(
                                    <DatePicker
                                        format="YYYY-MM-DD"
                                        style={{
                                            width: 250
                                        }}
                                        placeholder="请输入开始日期"
                                    />
                                )}
                            </FormItem>
                            <FormItem label="桩号范围" style={{height: 65}}>
                                {getFieldDecorator("mileage", {
                                    initialValue: current.mileage,
                                    rules: [
                                        {
                                            required: true,
                                        }
                                    ],
                                })(<Input disabled style={{
                                    width: 250
                                }}/>)}
                            </FormItem>
                            <FormItem label="备注" style={{height: 65}}>
                                {getFieldDecorator("remark", {
                                    initialValue: current.remark ? current.remark : '',
                                })(
                                    <>
                                        <TextArea maxLength="100" style={{
                                            width: 250,
                                        }} autosize={{minRows: 4, maxRows: 4}} onChange={handleInputValue}
                                        >
                                        </TextArea>
                                    </>
                                )}
                                <TextAreaSpan>({textAreaValue.length}/100)</TextAreaSpan>
                            </FormItem>
                        </Col>
                    </Row>
                </Form>
            </FormLayOut>
        </Modal>
    );
});
//台座状态弹窗
export const ModalForm = Form.create()(props => {
    const {form, title, visible, handleCancel, current, disableSelect, textAreaValue, handleInputValue, handleBodyNameChange, handleBridgeChange} = props;
    const {getFieldDecorator, getFieldValue} = form;
    if (Object.keys(current).length == 0) {
        return null
    }
    if (!current.bridges || !current.bridges.length) {
        return null
    }
    console.log(current, 'current');
    // 若下一步状态对应的台座类型不变（仍为钢筋绑扎台座或者预制台座），则自动提取当前台座的台座类型及台座名称，台座类型不支持修改，台座名称支持修改
    // 若下一步状态对应的台座类型改变（不再是钢筋绑扎台座或者预制台座），则自动提取状态对应的台座类型，不支持修改；台座名称默认为空，支持下拉选择对应的空闲的台座名称
    //  台座空闲时桥梁名称、桩号、梁体类型、梁体名称这4个字段均默认为空，选择时需过滤已被选择过的梁体信息，选择完后仍支持切换选择项
    const isDisableStageType = current.curStage != current.nextStage;
    const isDisablePedestal = current.nextStage>=30;
    //  台座使用时自动提取当前梁体的信息，均不支持修改
    const isFree = !current.bodyId    //是否空闲
    const currentPedestal = current.pedestalList.find(pedestal => pedestal.v == current.name);
    const currentBridge = current.bridges.find(bridge => bridge.v == current.bridgeName);
    const currentBodyType = current.bodyTypes.find(bodyType => bodyType.v == current.typeName);
    const disabledEndDate = (endValue) => {
        const defaultValue = current.beginDate;
        const currentTime = moment();
        const startValue = currentTime;
        if (!endValue || !startValue) {
            return false;
        }
        return  endValue.valueOf() < defaultValue;
    }
    console.log(disableSelect, 'disableSelect');
    const handleOk = () => {
        form.validateFields((err, fieldsValue) => {
            if (err) return;
            const params = {
                "beginDate": fieldsValue['planBeginDate'].format('YYYY-MM-DD'),
                "bodyId": isFree ? fieldsValue['bodyName'] : current.bodyId,
                "pedestalId": fieldsValue['pedestalName'],
                "remark": textAreaValue,
                "stateId": current.stateId,
                "userName": fieldsValue['userName']
            }
            if (current.first) {
                confirm({
                    title: '梁场进入生产状态后，梁体阶段状态模板将禁止修改，是否确认操作？',
                    okText: '取消',
                    cancelText: '确认',
                    onOk() {
                        console.log('OK');
                    },
                    onCancel() {
                        api.setBodyNext(params).then(res => {
                            if (res.code == 1) {
                                message.success('设置成功');
                                handleCancel()
                            }
                        })
                    },
                });
            } else {
                api.setBodyNext(params).then(res => {
                    if (res.code == 1) {
                        message.success('设置成功');
                        handleCancel()
                    }
                })
            }
        });
    };
    return (
        <Modal
            maskClosable={false}
            width={800}
            title={title}
            visible={visible}
            onCancel={handleCancel}
            destroyOnClose
            footer={[
                <Button key="ok" type="primary" onClick={handleOk}>
                    确定
                </Button>,
                <Button key="cancel" onClick={handleCancel}>
                    取消
                </Button>
            ]}
        >
            <FormLayOut>
                <Form layout="inline" labelAlign='left'>
                    <Row>
                        <Col span={12}>
                            <FormItem label="下一步状态:" style={{height: 65}}>
                                {getFieldDecorator('nextStateName', {
                                    rules: [
                                        {
                                            required: true,
                                        },
                                    ],
                                    initialValue: current.stateName,
                                })(<Input disabled placeholder="请输入" style={{
                                    width: 250
                                }}/>)}
                            </FormItem>
                            <FormItem label="台座类型" style={{height: 65}}>
                                {getFieldDecorator("stageName", {
                                    initialValue: current.stageName,
                                    rules: [
                                        {
                                            required: true,
                                        },
                                    ],
                                })(
                                    (<Input disabled={true} placeholder="请输入" style={{
                                        width: 250
                                    }}/>)
                                )}
                            </FormItem>
                            <FormItem label="桥梁名称" style={{height: 65}}>
                                {getFieldDecorator('bridgeName', {
                                    rules: [
                                        {
                                            required: true,
                                            message: '请选择桥梁名称'
                                        }
                                    ],
                                    initialValue: current.bridges.length ? (isFree ? '' : currentBridge ? currentBridge.k : '') : ''

                                })(<Select
                                    onChange={(val) => handleBridgeChange(val, getFieldValue('typeName'))}
                                    disabled={!isFree}
                                    placeholder="请选择"
                                    style={{
                                        width: 250
                                    }}
                                >
                                    {
                                        current.bridges.length &&
                                        current.bridges.map(item => (
                                            <Option value={item.k} key={item.v}>{item.v}</Option>
                                        ))
                                    }
                                </Select>)}
                            </FormItem>
                            <FormItem label="梁体名称" style={{height: 65}}>
                                {getFieldDecorator('bodyName', {
                                    rules: [
                                        {
                                            required: true,
                                            message: '请选择梁体名称'
                                        },
                                        // {
                                        //     validator: compareName,
                                        // }
                                    ],
                                    initialValue: current.bodyName,
                                })(isFree ? <Select
                                    style={{width: 250}}
                                    placeholder="请选择梁体名称"
                                    onChange={(value) => handleBodyNameChange(value, current.freeStatusTypeNameList.find(item => item.bodyId == value))}
                                    disabled={disableSelect}
                                >
                                    {
                                        current.freeStatusTypeNameList.length &&
                                        current.freeStatusTypeNameList.map(item => (
                                            <Option value={item.bodyId} key={item.bodyName}>{item.bodyName}</Option>
                                        ))
                                    }
                                </Select> : <Input disabled={!isFree} placeholder="请输入" style={{
                                    width: 250
                                }}/>)}
                            </FormItem>
                            <FormItem label="梁体类型" style={{height: 65}}>
                                {getFieldDecorator("typeName", {
                                    initialValue: current.typeName,
                                    rules: [
                                        {
                                            required: true,
                                            message: '请输入梁体类型'
                                        }
                                    ],
                                })(
                                    <Input disabled={true} placeholder="请输入梁体类型" style={{width: 250}}/>
                                )}
                            </FormItem>

                            <FormItem label="负责人" style={{height: 65}}>
                                {getFieldDecorator('userName', {
                                    rules: [
                                        {
                                            required: true,
                                            message: '请选择负责人!'
                                        }
                                    ],
                                    initialValue: isFree ? '' : current.userName,
                                })(
                                    <Select style={{width: 250}}
                                            placeholder="请选择负责人">
                                        {
                                            current.userList.length &&
                                            current.userList.map(item => (
                                                <Option value={item.userName}
                                                        key={item.userName}>{item.realName}</Option>
                                            ))
                                        }
                                    </Select>
                                )}
                            </FormItem>
                        </Col>
                        <Col
                            span={12}
                            style={{
                                height: 65
                            }}
                        >
                            <FormItem label="开始时间" style={{height: 65}}>
                                {getFieldDecorator("planBeginDate", {
                                    initialValue: current.beginDate
                                        ? moment(current.beginDate)
                                        : null,
                                    rules: [
                                        {
                                            required: true,
                                            message: '请选择开始时间!'
                                        }
                                    ],
                                })(
                                    <DatePicker
                                        disabledDate={disabledEndDate}
                                        format="YYYY-MM-DD"
                                        style={{
                                            width: 250
                                        }}
                                        placeholder="请输入开始日期"
                                    />
                                )}
                            </FormItem>
                            <FormItem label="台座名称" style={{height: 65}}>
                                {getFieldDecorator('pedestalName', {
                                    rules: [
                                        {
                                            required: isDisablePedestal?false:true,
                                            message: '请选择台座名称!'
                                        }
                                    ],
                                    initialValue: isFree ? current.pedestalId : (current.pedestalList.length ? (isDisableStageType ? '' : currentPedestal ? currentPedestal.k : '') : ''),
                                })(<Select
                                    disabled={isFree||isDisablePedestal}
                                    style={{width: 250}}
                                    placeholder="请选择台座名称"
                                >
                                    {
                                        current.pedestalList.length &&
                                        current.pedestalList.map(item => (
                                            <Option value={item.k} key={item.v}>{item.v}</Option>
                                        ))
                                    }
                                </Select>)}
                            </FormItem>
                            <FormItem label="桩号范围" style={{height: 65}}>
                                {getFieldDecorator("mileage", {
                                    initialValue: current.mileage,
                                    rules: [
                                        {
                                            required: isFree ? false : true,
                                            message: '请选择桩号!'
                                        }
                                    ],
                                })(
                                    <Input disabled={true} placeholder="请输入桩号"
                                           style={{width: 250}}/>
                                )}
                            </FormItem>
                            <FormItem label="备注" style={{height: 65}}>
                                {getFieldDecorator('remark', {
                                    initialValue: current.remark,
                                })(
                                    <TextArea style={{width:250}} maxLength="100" autosize={{minRows: 4, maxRows: 4}} onChange={handleInputValue}>
                                    </TextArea>
                                )}
                                <TextAreaSpan>({textAreaValue.length}/100)</TextAreaSpan>
                            </FormItem>
                        </Col>
                    </Row>
                </Form>
            </FormLayOut>
        </Modal>
    );
});